@import "./vars";

body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1568px;
    margin: 0 auto;
    padding: 0 15px;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

a {
    text-decoration: none;
}